import { Component, OnInit } from '@angular/core';
import { Section } from 'src/models/Section';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { Factory } from 'src/models/Factory';
import { ToastText, ErrorMessage, MenuText, ErrorType } from 'src/commons/Constants';
import { ActionSheetController, ModalController, PopoverController } from '@ionic/angular';
import { SecaoMenuPage } from './secao-menu/secao-menu.page';
import { NewPageS } from './new/new.page';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-secao',
  templateUrl: './secao.page.html',
  styleUrls: ['./secao.page.scss'],
})
export class SecaoPage implements OnInit {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "SecaoPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  private sections: Array<Section> = new Array<Section>();
  public sectionList: Array<Section> = new Array<Section>();
  private searchTerm: string;
  private name: string;
  private factoryId: number;
  private factories: Array<Factory> = new Array<Factory>();
  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    private api: BackendApiProvider,
    private utils: Utils,
    public actionSheetController: ActionSheetController,
    public modalController: ModalController,
    public popoverController: PopoverController,
    private pageDataService: PageDataService,
    private router: Router
  ) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.pageDataService.listUserEvent.subscribe(() => {
      let data = this.pageDataService.getData('newSection');
      if (!Utils.isVarInvalid(data)) {
        this.sections.splice(this.sections.indexOf(this.sections.filter(n => n.getId() == data.getId())[0]), 1);
        this.sections.push(data);
        this.filter(this.searchTerm)
      }
    })
    this.getSections()
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  ngOnInit() {
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getSections() {
    this.api.getFactories().then((factories: Array<Factory>) => {
      this.factories = factories;
      this.api.getSections().then((sections: Array<Section>) => {
        this.sections = sections;
        this.sectionList = this.sections
      }).catch((error) => {
        this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
      });
    }).catch((error) => {
      this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isVarInvalid(evt)) {
      this.sectionList = this.sections;
      return;
    }
    this.searchTerm = evt;
    if (!this.searchTerm) {
      this.sectionList = this.sections;
      return;
    }
    this.sectionList = this.sections.filter(n => n.getNome().toLowerCase().includes(this.searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async abrirActionSheet(section: Section) {
    const actionSheet = await this.actionSheetController.create({
      header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
      buttons: [
        {
          text: MenuText.EDIT,
          handler: () => {
            this.editSection(section);
          }
        }, {
          text: MenuText.DELETE,
          role: 'destructive',
          handler: () => {
            this.api.removeSection(section.getId()).then(() => {
              this.utils.simpleToast(ToastText.REMOVE_SUCCESSFUL);
              this.sections.splice(this.sections.indexOf(section), 1);
              this.filter(this.searchTerm);
            }).catch((error) => {
              let message = JSON.parse(error._body).message as string;
              if (message.includes(ErrorType.OBJECT_IN_USE)) {
                this.utils.simpleToastError(ToastText.OBJECT_IN_USE);
              }
              else {
                this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
              }
            });
          }
        },
        {
          text: MenuText.CANCEL, role: 'cancel',
          handler: () => {
          }
        }]
    });
    await actionSheet.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async editSection(section: Section = undefined) {
    const modal = await this.modalController.create({
      component: NewPageS,
      animated: true,
      showBackdrop: false,
      cssClass: 'popover_class',
      componentProps: {
        section: section
      }
    });
    return await modal.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async openMenu(ev: any) {
    const popover = await this.popoverController.create({
      component: SecaoMenuPage,
      event: ev,
      animated: true,
      showBackdrop: true,
      translucent: true,
      cssClass: 'popover_class'
    });
    return await popover.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  navigateDashboard() {
    this.router.navigate(['admin', 'dashboard'])
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
